
.container{
    margin: 0 auto;
    @media (min-width:320px) {
        padding: 0 12px;
        width: 100%;
        
    }
    @media (min-width:375px){
        padding: 0 20px;
        
    }
    @media (min-width:768px){
        max-width: 704px;
        padding: 0;
    }
    @media (min-width:1200px){
        max-width: 1136px;
    }

  }
img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul,
ol {
  list-style-type: none;
  padding: 0;
}
a {
  text-decoration: none;
}
body {
  font-family: Montserrat;
  font-style: normal;
}

